.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  width: 200px;
  margin: 0;
  padding: 16px;
  transition: transform .3s;
  display: flex;
  position: relative;
}

.card__img {
  justify-content: center;
  width: 160px;
  height: 134px;
  margin: 16px 3px 0;
}

.card__title {
  color: #060b35;
  margin: 40px 0 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  align-items: flex-end;
  margin: 4px 0 0;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: flex-end;
  width: 166px;
  margin-top: 24px;
  display: flex;
}

.card__price-title {
  color: #616070;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__price-numder {
  color: #060b35;
  text-align: right;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card:hover {
  transform: scale(1.2);
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: color, background-color .3s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.rating {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  height: 16px;
  margin-top: 16px;
  display: flex;
}

.stars {
  justify-content: center;
  align-items: center;
  width: 96px;
  height: 16px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;
  justify-content: space-between;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--4 :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.reviews {
  color: #060b35;
  text-align: right;
  flex-direction: column;
  justify-content: flex-end;
  width: 53px;
  height: 16px;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.catalog {
  grid-template-rows: repeat(auto-fit, 408px);
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.header {
  box-sizing: border-box;
  background: #fff;
  flex-shrink: 0;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  margin: auto;
  padding: 0 50px;
  display: flex;
}

.nav {
  margin: 0;
  padding: 0;
}

.nav__list {
  color: #060b35;
  text-transform: uppercase;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #060b35;
  justify-content: center;
  align-items: center;
  height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__item:nth-child(n+2) {
  margin-left: 20px;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.logo {
  width: 40px;
  height: 40px;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

